/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */

import { __ } from '@wordpress/i18n';
import { useBlockProps, RichText, InspectorControls, MediaUpload, MediaUploadCheck, InnerBlocks  } from '@wordpress/block-editor';
import {
	PanelBody,
	PanelRow,
	QueryControls,
	ToggleControl,
	FormTokenField,
	SelectControl,
	TextControl
} from '@wordpress/components';
import ServerSideRender from '@wordpress/server-side-render';
import { useSelect } from '@wordpress/data';
import { RawHTML, useState } from '@wordpress/element';
import './editor.scss';
import { name } from './block.json';
export default function Edit( { attributes, setAttributes } ) {

const blockProps = useBlockProps();
const { numberOfItems, displayExcerpt, displayThumbnail, simpleCategory, postCats, message } = attributes;

const [selectedCategories, setSelectedCategories] = useState([]);

const { categories } = useSelect((select) => {
	const { getEntityRecords } = select("core");
  
	return {
	  categories: getEntityRecords("taxonomy", "category", {
		per_page: -1, // fetch all categories
	  }),
	};
  });
  
  let categoryOptions = [];
  
  if (categories) {
	categoryOptions = categories.map((category) => {
	  return {
		label: category.name,
		value: category.id,
	  };
	});
  }

  console.log(postCats)

	const posts = useSelect(
		( select ) => {
			return select( 'core' ).getEntityRecords( 'postType', 'post', {
				'per_page': numberOfItems,
				'_embed': true,
				'categories': postCats,
			});
		},
		[ numberOfItems , postCats ]
	);


	
	return (

<>
		<InspectorControls>
			<PanelBody title={ __( 'Content Settings', 'slider-news' ) }>
			<PanelRow>
		<fieldset>
			<TextControl
				label={__( 'Slider Title', 'slider-news' )}
				value={ message }
				onChange={ ( newMessage ) =>
					setAttributes( { message: newMessage } )
				}
				help={ __( '', 'slider-news' )}
			/>
		</fieldset>
		</PanelRow>
				<PanelRow>
					<QueryControls 
						numberOfItems={ numberOfItems }
						onNumberOfItemsChange={ ( value ) =>
							setAttributes( { numberOfItems: value } )
						}
						minItems={ 1 }
						maxItems={ 30 }
					/>
				</PanelRow>
				<PanelRow>
					<ToggleControl
						label={ __( 'Show Featured Image', 'slider-news' ) }
						checked={ displayThumbnail }
						onChange={ () =>
							setAttributes( { displayThumbnail: ! displayThumbnail } )
						}
					/>
				</PanelRow>
				<PanelRow>
					<ToggleControl
						label={ __( 'Display Excerpt', 'slider-news' ) }
						checked={ displayExcerpt }
						onChange={ () =>
							setAttributes( { displayExcerpt: ! displayExcerpt } )
						}
					/>
				</PanelRow>
				<PanelRow>

				<SelectControl
  label={__("Categories", "slider-news")}
  multiple
  value={postCats}
  options={categoryOptions}
  onChange={(value) => {
    setAttributes({ postCats: value }); // Update postCats attribute
    setSelectedCategories(value); // Update selectedCategories state
  }}
/>




				</PanelRow>
			</PanelBody>
		</InspectorControls>



		<div { ...useBlockProps() }>


	<div className="row mb-2">
      <div className="col-12 col-lg-8 col-xxl-7 text-center mx-auto">
		<RichText
				tagName="h2"
				className="display-5 mt-2 mb-3"
				value={ message }
				onChange={ ( newMessage ) =>
					setAttributes( { message: newMessage } )
				}
			/>
      </div>
    </div>

		<div className="slider" id="sliderPost">
				{ posts && posts.map( ( post ) => {
					return (
						<a className="col-12 col-md-4 mb-4 position-relative slider-col" href={ post.link } key={ post.id }>
							<div className="p-4 slider-content-wrapper">
							{
								displayThumbnail && 
								post._embedded && 
								post._embedded['wp:featuredmedia'] &&
								post._embedded['wp:featuredmedia'][0] &&
								<img 
								className='wp-block-slider-news__post-thumbnail'
									src={ post._embedded['wp:featuredmedia'][0].media_details.sizes.medium.source_url }
									alt={ post._embedded['wp:featuredmedia'][0].alt_text }
								/>
							}
							<span className="slide-title mb-3">
							{ post.title.rendered ? (
									<RawHTML>
										{ post.title.rendered }
									</RawHTML>
								) : (
									__( 'Default title', 'slider-news' )
								)}
							</span>
							{
								displayExcerpt &&
								post.excerpt.rendered && (
									<span>
										<RawHTML>
											{ post.excerpt.rendered }
										</RawHTML>
									</span>
								)
							}
							</div>
						</a>
					)
				})}
			</div>
		</div>

	</>

	);
}
